:root{
    --primary:#51b52e;
    --secondary: #000;
    --text-primary: #51b52e;
    --text-secondary: #000;
    --text-9: #999;
    --text-6: #666;
    --text-white: #fff;
    --poppins:'Poppins', sans-serif;
    --roboto: 'Roboto', sans-serif;
    --trans: all .3s ease;
    --btn-shade: 0 0 10px 2px rgba(0,0,0,.10);
    --shade:0 0 10px 10px rgba(0,0,0,.10);
  }
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
h1,h2,h3,h4,h5,h6,ul,p,label,li{
  margin: 0;
  padding: 0;
  list-style: none;
}
body {
   max-width: 100%;
   overflow-x: hidden;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
a, a:hover{
